<template>
  <v-label :label="props.label">
    <slot v-if="isFilled" />
    <span
      v-else
      class="italic text-zinc-400 dark:text-gray-400"
    >
      Not filled
    </span>
  </v-label>
</template>

<script setup lang="ts">
const props = defineProps<{
  label: string
}>()

const slots = useSlots()
const isFilled = computed(() => {
  const nodes = slots.default?.()
  if (!nodes) {
    return false
  }

  const textNodes = nodes.filter((node) => node.patchFlag === 1) // can't import vue's core enum :(
  if (!textNodes.length) {
    return true
  }

  return textNodes.some((node) => {
    if (typeof node.children === "string") {
      return Boolean(node.children.trim())
    }

    return false
  })
})
</script>
