<template>
  <div>
    <v-h1>Overview</v-h1>

    <v-info
      v-if="data?.kyc_status !== KYCStatus.Ok"
      class="mb-5"
    >
      Missing fields (except email, address and phone) can be automatically populated after your
      <NuxtLink to="/verify-identity">identity verification</NuxtLink>
    </v-info>

    <template v-if="data">
      <div class="mb-4 flex items-center gap-4">
        <v-h2>{{ data.name }} {{ data.surname }}</v-h2>

        <v-button
          variant="primary"
          to="/profile/edit"
        >
          Edit information
        </v-button>
      </div>

      <div class="flex flex-col gap-5">
        <div class="flex gap-5">
          <v-labeled-detail label="ID">
            {{ data.id }}
          </v-labeled-detail>
          <v-labeled-detail label="Identity verification status">
            {{ data.kyc_status_translated }}
          </v-labeled-detail>
        </div>

        <div>
          <v-h3 class="mb-2">Contact information</v-h3>
          <div class="flex gap-5">
            <v-labeled-detail label="Email">
              {{ data.email }}
            </v-labeled-detail>
            <v-labeled-detail label="Phone">
              {{ data.phone }}
            </v-labeled-detail>
            <v-labeled-detail label="Address">
              {{ data.address }}
            </v-labeled-detail>
          </div>
        </div>

        <div>
          <v-h3 class="mb-2">Personal information</v-h3>
          <div class="flex gap-5">
            <v-labeled-detail label="Sex">
              {{ data.sex }}
            </v-labeled-detail>
            <v-labeled-detail label="Birthday date">
              {{ data.birthday_date }}
            </v-labeled-detail>
            <v-labeled-detail label="Nationality">
              {{ getCountryName(data.nationality_country_code) }}
            </v-labeled-detail>
            <v-labeled-detail label="Document type">
              {{ data.document_type ? getDocumentTypeName(data.document_type) : "" }}
            </v-labeled-detail>
            <v-labeled-detail label="Document country of issue">
              {{ getCountryName(data.document_country_code) }}
            </v-labeled-detail>
            <v-labeled-detail label="Document issuance date">
              {{ data.document_issuance_date }}
            </v-labeled-detail>
            <v-labeled-detail label="Document expiration date">
              {{ data.document_expiration_date }}
            </v-labeled-detail>
            <v-labeled-detail label="Tax number">
              {{ data.tax_number }}
            </v-labeled-detail>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script setup lang="ts">
import { countries } from "countries-list"
import { KYCStatus } from "~/types/api"

const { data } = storeToRefs(useUserStore())

function getCountryName(code: null | string) {
  if (!code || !(code in countries)) {
    return code
  }

  return countries[code as keyof typeof countries].name
}
</script>
